import React, { useState, useEffect } from "react";
import {
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CRow,
} from "@coreui/react";
import API from "src/api";

const NotesList = (props) => {

  const fields = ["chapter", "topic", "description", "approve", "delete"];
  const [notesData, setNotesData] = useState([{}]);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    getData();
  }, []);

  async function getData() {
    try {
      setIsLoading(true);
      var query = "?populate=topicId&populate=chapterId&populate=userId&dsc=createdAt&isPublic=true";
      var response = await API.get("/notes/list" + query);
      setNotesData(response.data.data);
      setIsLoading(false);
    } catch (error) {
    }
  }

  const deleteNotes = async (id) => {
    try {
      var response = await API.delete("/notes/" + id);
      if (response.status === 201) {
        var data = notesData.filter((a) => {
          return a._id !== id;
        });
        setNotesData(data);
      }
    } catch (error) {
    }
  };

  const changeNotesStatus = async (item) => {
    try {
      setIsLoading(true);
      var response = await API.post("/notes/changeNotesStatus/" + item._id, {
        "status": !item.isApprove
      });
      if (response.status === 201) {
        for (var i in notesData) {
          if (notesData[i]._id === item._id) {
            notesData[i] = response.data.data;
            console.log(response.data);
            break;
          }
        }
        setNotesData(notesData);
      }
      setIsLoading(false);
    } catch (error) {
    }
  }

  return (
    <>
      <div className="">
        <CRow className="p-2 ml-auto"></CRow>
        <CRow>
          <CCol xs="12">
            <CCard>
              <CCardHeader>
                <span className="h4">Notes List</span>
              </CCardHeader>
              <CCardBody>
                {isLoading ? (
                  <div>Loading...</div>
                ) : (
                  <CDataTable
                    items={notesData}
                    fields={fields}
                    itemsPerPage={10}
                    loading={isLoading}
                    pagination
                    scopedSlots={{
                      description: (item) => (
                        <td>
                          {item.description !== undefined &&
                            <div dangerouslySetInnerHTML={{ __html: item.description.replace(/(<? *script)/gi, 'illegalscript') }} >
                            </div>
                          }
                        </td>
                      ),
                      chapter: (item) => (
                        <td>
                          {item.chapterId !== undefined ? (item.chapterId.preFix + " : " + item.chapterId.title) : "-"}
                        </td>
                      ),
                      topic: (item) => (
                        <td>
                          {item.topicId !== undefined ? (item.topicId.title) : "-"}
                        </td>
                      ),
                      approve: (item) => (
                        <td>
                          <CButton
                            className={item.isApprove ? "btn btn-pill mx-2 btn-success" : "btn btn-pill mx-2 btn-warning"}
                            onClick={() => {
                              changeNotesStatus(item)
                            }}
                          >
                            {item.isApprove ? "Approve" : "Pending"}
                          </CButton>
                        </td>
                      ),
                      delete: (item) => (
                        <td>
                          <CButton
                            className="btn btn-danger"
                            onClick={(e) => {
                              deleteNotes(item._id);
                            }}
                          >
                            Delete
                          </CButton>
                        </td>
                      ),
                    }}
                  />
                )}
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </div>
    </>
  );
};

export default NotesList;
